@use "../global/variables" as *;

.social {
  padding: 20px 0;
  border-top: 1px solid $grey-light;
  border-bottom: 1px solid $grey-light;

  @media (min-width: $tablet) {
    padding: 0;
    border: 0;
  }
}

.social__list {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 24px;
  padding: 0;
  list-style: none;
}

.social__link {
  &:active {
    opacity: 0.3;
  }
}
