@use "../global/variables" as *;

.navigation__wrapper {
  width: 100%;
}

.navigation__list {
  display: flex;
  flex-flow: column nowrap;
  list-style: none;
  margin: 0;
  padding: 0;

  @media (min-width: $tablet) {
    flex-flow: row nowrap;
    gap: 33px;
  }

  @media (min-width: $desktop) {
    &--inverted .navigation__link-text {
      color: $secondary-color;
    }

    &--inverted .navigation__link-text--current {
      border-color: $secondary-color;
    }
  }
}

.navigation__item {
  border-top: 1px solid $color-line-navigation;

  @media (min-width: $tablet) {
    border: 0;
  }

  &:last-child {
    border-bottom: 1px solid $color-line-navigation;

    @media (min-width: $tablet) {
      border: 0;
    }
  }
}

.navigation__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 22px 20px 24px;
  font-family: $secondary-font-family;
  font-size: 20px;
  font-weight: 400;
  line-height: 20px;
  text-decoration: none;
  text-transform: uppercase;
  color: $primary-text-color;

  @media (min-width: $tablet) {
    padding: 0;
    font-size: 18px;
  }

  @media (min-width: $desktop) {
    font-size: 18px;
  }

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.navigation__link-text {
  padding: 0 0 5px;
  margin: 0 0 -5px;

  &--current {
    border-bottom: 2px solid $primary-color;
  }
}
